<template>
    <v-flex xs12 >
  <v-container fluid>      
  <v-form ref="form" v-model="valid" lazy-validation name="submit-to-google-form">
    <v-text-field
      v-model="name"
      :rules="nameRules"
      :counter="20"
      label="Nama lengkap"
      required
      outline
      name="entry.726740487"
    ></v-text-field>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="Alamat e-mail"
      required
      outline
      name="entry.1410098018"
    ></v-text-field>
    <v-textarea
      v-model="textarea"
      label="Keperluan Anda"
      outline
      name="entry.1998691914"
    ></v-textarea>    
    <v-text-field
      v-model="honeypot"
      label=" "
      class="honeypot"
    ></v-text-field>    
    <v-btn
      color = "secondary"
      :disabled="!valid"
      @click="submit"
    >
      submit
    </v-btn>
    <v-btn 
    color = "secondary"
    @click="clear"
    >
    clear</v-btn>
  </v-form>
    <v-spacer></v-spacer>
<a class="ctrlq fb-button" href="javascript:void(0);" @click="openMessenger"></a>
  </v-container>
    </v-flex>

</template>

<script>
import CryptoJS from 'crypto-js/crypto-js'

var cipher_key = "U2FsdGVkX19LIRVpXKgXktK+An43KRL7LkKFfMtiVkY="
var cipher_url = "U2FsdGVkX1/NIKeGvtkET6h197ZQiXXh9UDLKu04L75cL19KLgpPVeJsBjPJfybLrGeVYt26BjR3zsUd2h5TMTGJbIzfvpXIp21XgxyM8AEYvByxADRH+lfQL5u8J8JuWmwueiyMjmHnBmfQI2yA07rtSKfBNs5lGZGkVyoBnX8="
var secret = CryptoJS.AES.decrypt(cipher_key, '123456').toString(CryptoJS.enc.Utf8);
var url = CryptoJS.AES.decrypt(cipher_url, secret).toString(CryptoJS.enc.Utf8);

  export default {
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Nama lengkap diperlukan',
        v => (v && v.length <= 20) || 'Nama harus kurang dari 20 karakter'
      ],
      email: '',
      emailRules: [
        v => !!v || 'Alamat e-mail diperlukan',
        v => /.+@.+/.test(v) || 'Alamat e-mail harus valid'
      ],
      textarea: '',      
      honeypot: '',
    }),

    methods: {
      submit () {
        if (this.honeypot == "" && this.$refs.form.validate()) {
        var form = document.forms['submit-to-google-form']
        var formData = new FormData(form);           
      this.$http.post(url, formData).then(function(response){
        console.log(response)
      })
        }
      },
      clear () {
        this.$refs.form.reset()
      },
    openMessenger() {
//        window.open('//m.me/kristian.irwanto', 'messenger', 'top=0, left=' + (screen.width-680) + ', width=680, height=' + screen.height);
        window.open('//m.me/kristian.irwanto', '_blank');        
    }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.honeypot {
  display: none;
}
.ctrlq.fb-button,.ctrlq.fb-close{
    position:fixed;right:24px;cursor:pointer
}
.ctrlq.fb-button{
    z-index:1;background:url(data:image/svg+xml;base64,) center no-repeat #0084ff;width:60px;height:60px;text-align:center;bottom:24px;border:0;outline:0;border-radius:60px;-webkit-border-radius:60px;-moz-border-radius:60px;-ms-border-radius:60px;-o-border-radius:60px;box-shadow:0 1px 6px rgba(0,0,0,.06),0 2px 32px rgba(0,0,0,.16);-webkit-transition:box-shadow .2s ease;background-size:80%;transition:all .2s ease-in-out
}
.ctrlq.fb-button:focus,.ctrlq.fb-button:hover{
    transform:scale(1.1);box-shadow:0 2px 8px rgba(0,0,0,.09),0 4px 40px rgba(0,0,0,.24)
}
</style>